import "core-js/modules/es.function.name.js";
import { computed, defineComponent } from "vue"; // import { useStore } from "vuex"; //由于vuex对ts的不够友好，此处直接用useStore的state不会提示state中的数据，因此此处不用默认的useStore

import { useStore } from "@/store";
import UserMsg from "./user-msg.vue";
import LocalCache from "@/utils/cache";
import { useRouter } from "vue-router";
export default defineComponent({
  components: {
    UserMsg: UserMsg
  },
  setup: function setup() {
    var store = useStore();
    var name = computed(function () {
      return store.state.login.userInfo.name;
    });
    var router = useRouter(); // 退出

    var loginOut = function loginOut() {
      LocalCache.deleteCache("token");
      router.push("/main");
    };

    return {
      name: name,
      loginOut: loginOut
    };
  }
});